import {action, observable, runInAction} from "mobx";
import {User} from "../types/FriendsLists";
import {hand, RestApiMethods} from "../helper/hand";
import {PageId} from "../helper/GetPage";
import {USERID} from "../index";
import {SetNotification} from "../helper/SetNotification";
import {Connection} from "../Connection";
import {ConvertToPQArray} from "../helper/ConvertToPQArray";

export interface CoolUser {
	friends: number[]
	subscribers: number[]
	avatar: string;
	name: string;
	surname: string;
}

interface UsersStoreI {
	yourselves: CoolUser | null;
	pageOwner: CoolUser | null;
	mutualFriends: number[];
	users: User[];
	onlineUsers: number[];
	onlineFriends: number[];
	gotUser: number[];

	reactOnlineUsers: (res: any) => void;
	reactToDisconnect: (userId: number) => void;
	reactToConnect: (userId: number) => void;

	start(): void;
	setMutualFriends(): void;

	getSomebody(offset: number, array: number[]): Promise<User[]>;

	getUsers(usersId: number[]): Promise<User[]>;
	subscribe(userId: number): void;
	unsubscribe(userId: number): void;
	addFriend(userId: number): void;
	deleteFriend(userId: number): void;
}

export const UsersStore = observable<UsersStoreI>({
	yourselves: null,
	pageOwner: null,
	mutualFriends: [],
	gotUser: [],
	users: [],
	onlineUsers: [],
	onlineFriends: [],

	start: action(async () => {
		const res = await hand(`/api/user/friends_and_subs/${PageId}?userId=${USERID}`, RestApiMethods.Get);
		if (res?.status !== 200) {
			SetNotification(`Error: ${res?.status} ${res?.statusText}`);
			return;
		}
		const data: {client: CoolUser, user: CoolUser} = await res?.json();
		data.user.friends.qSort();
		data.user.friends.isSorted = true;
		data.user.friends.remove(0);
		data.user.subscribers.qSort();
		data.user.subscribers.isSorted = true;
		data.user.subscribers.remove(0);
		if (data.client.name !== '') {
			data.client.friends.qSort();
			data.client.friends.isSorted = true;
			data.client.friends.remove(0);
			data.client.subscribers.qSort();
			data.client.subscribers.isSorted = true;
			data.client.subscribers.remove(0);
			runInAction(() => {
				UsersStore.yourselves = data.client;
				UsersStore.pageOwner = data.user;
				UsersStore.gotUser.insert(PageId);
				if (USERID) UsersStore.gotUser.insert(+USERID);
			})
		} else {
			runInAction(() => {
				UsersStore.pageOwner = data.user;
			})
		}
		if (UsersStore.yourselves === null || UsersStore.pageOwner === null) {
			return;
		}
		const uniqueIdOfUsersArray: number[] = [];
		const arrayOfAll = UsersStore.yourselves.friends.concat(UsersStore.yourselves.subscribers).concat(UsersStore.pageOwner.friends).concat(UsersStore.pageOwner.subscribers);
		for (let i = 0; i < arrayOfAll.length; i++) {
			uniqueIdOfUsersArray.insert(arrayOfAll[i]);
		}
		UsersStore.setMutualFriends();
		Connection.start(uniqueIdOfUsersArray);
	}),

	getUsers: action(async (usersId: number[]) => {
		for (let i = 0; i < usersId.length; i++) {
			if (UsersStore.gotUser.search(usersId[i]) >= 0) {
				usersId.remove(usersId[i]);
			} else {
				UsersStore.gotUser.insert(usersId[i]);
			}
		}
		if (usersId.length < 1) {
			return [];
		}
		const res = await hand(`/api/user/friends_users?idOfUsers=${ConvertToPQArray(usersId)}`, RestApiMethods.Get);
		if (res?.status !== 200) {SetNotification(`Error: ${res?.status} ${res?.statusText}`); return [];}
		const data: {users: User[]} = await res?.json();
		const list: User[] = [...UsersStore.users];
		for (let i = 0; i < data.users.length; i++) {
			list.insertObj(data.users[i], 'ID');
		}
		runInAction(() => {
			UsersStore.users = list;
		})
		return data.users;
	}),

	setMutualFriends: action(() => {
		const friends: number[] = [];
		if (UsersStore.yourselves === null || UsersStore.pageOwner === null) {return}
		for (let i = 0; i < UsersStore.pageOwner.friends.length; i++) {
			const index = UsersStore.yourselves.friends.search(UsersStore.pageOwner.friends[i]);
			if (index >= 0) {
				friends.insert(UsersStore.pageOwner.friends[i])
			}
		}
		UsersStore.mutualFriends = friends;
	}),

	reactOnlineUsers: action((res: number[]) => {
		UsersStore.onlineUsers = res;
		UsersStore.mutualFriends.isSorted = true;
		res.forEach(id => {
			if (UsersStore.pageOwner!.friends.indexOf(id) !== -1) {
				UsersStore.onlineFriends.insert(id);
			}
		})
	}),

	reactToDisconnect: action((userId: number) => {
		UsersStore.onlineUsers.remove(userId)
		if (UsersStore.pageOwner!.friends.indexOf(userId) !== -1) {
			UsersStore.onlineFriends.remove(userId);
		}
	}),

	reactToConnect: action((userId: number) => {
		UsersStore.onlineUsers.insert(userId);
		if (UsersStore.pageOwner!.friends.indexOf(userId) !== -1) {
			UsersStore.onlineFriends.insert(userId);
		}
	}),

	getSomebody: async (offset: number, array: number[]) => {
		if (UsersStore.yourselves === null || UsersStore.pageOwner === null) {return []}
		const needToReturn = array.slice(offset, offset + 20).filter(Boolean);
		const needToGet: number[] = [];
		const list: User[] = [];
		needToReturn.forEach((id: number) => {
			const index = UsersStore.users.searchObj<User, 'ID'>(id, 'ID');
			if (index < 0) {
				needToGet.insert(id);
			} else {
				list.insertObj(UsersStore.users[index], 'ID');
			}
		})
		const users = await UsersStore.getUsers(needToGet);
		users.forEach((user: User) => {list.insertObj(user, 'ID')})
		return list;
	},

	subscribe: action((userId: number) => {
		if (UsersStore.yourselves === null || !USERID) {return}
		hand('/api/user/subscribers/add/' + userId, RestApiMethods.Patch)
			.then (response => {
				if (response?.status === 204) {
					runInAction(() => {
						UsersStore.users.getByKey<User>(userId, 'ID').Subscribers.push(userId)
					})
				}
			})
	}),
	unsubscribe: action((userId: number) => {
		if (UsersStore.yourselves === null || !USERID) {return}
		hand('/api/user/subscribers/delete/' + userId, RestApiMethods.Patch)
			.then (response => {
				runInAction(() => {
					if (response?.status === 204) {
						const subs = UsersStore.users.getByKey<User>(userId, 'ID').Subscribers;
						subs.splice(subs.indexOf(userId, 1));
					}
				})
			})
	}),
	addFriend: action((userId: number) => {
		if (UsersStore.yourselves === null || !USERID) {return}
		hand('/api/user/friends/add/' + userId, RestApiMethods.Patch)
			.then (response => {
				const copy = JSON.parse(JSON.stringify(UsersStore.yourselves));
				copy!.friends.push(userId)
				runInAction(() => {
					if (response?.status === 204) {
						if (UsersStore.pageOwner!.friends.indexOf(userId) != -1) UsersStore.mutualFriends.insert(userId)
						UsersStore.yourselves = copy;
					}
				})

			})
	}),
	deleteFriend: action((userId: number) => {
		if (UsersStore.yourselves === null || !USERID) {return}
		hand('/api/user/friends/delete/' + userId, RestApiMethods.Patch)
			.then (response => {
				const copy = JSON.parse(JSON.stringify(UsersStore.yourselves));
				if (response?.status === 204) {
					const friends = copy!.friends;
					friends.splice(friends.indexOf(userId), 1);
				}
				runInAction(() => {
					const index = UsersStore.mutualFriends.indexOf(userId);
					if (index !== -1) UsersStore.mutualFriends.splice(index, 1);
					UsersStore.yourselves = copy
				})
			})
	}),
})