import React, {FC, useState, useCallback, useEffect} from 'react';
import {User} from "../../types/FriendsLists";
import {observer} from "mobx-react-lite";
import {UsersList} from "./UsersList";
import {PageNames} from "../MainPart";
import {CoolUser, UsersStore} from "../../store/UsersStore";

export const OtherList: FC = observer(() => {

    const [wait, setWait] = useState(true);
    const [index, setIndex] = useState(0);
    const users = (() => {
        const arr: User[] = [];
        if (!UsersStore.pageOwner) return [];
        UsersStore.pageOwner.subscribers.forEach((id) => {
            const necessaryUser = UsersStore.users.getByKeyIfExists<User>(id, 'ID');
            if (necessaryUser != undefined) arr.push(necessaryUser);
        })
        return arr;
    })()

    const [array, setArray] = useState<User[]>([...users]);

    const SetArray = useCallback((array: User[]) => {
        setArray(array);
    }, []);
    const SetWait = useCallback((wait: boolean) => {
        setWait(wait);
    }, []);
    const SetIndex = useCallback((index: number) => {
        setIndex(index);
    }, [])

    const getSubs = useCallback(() => {
        if ((UsersStore.pageOwner as CoolUser).subscribers.length > 0) {
            UsersStore.getSomebody(index * 20, (UsersStore.pageOwner as CoolUser).subscribers)
                .then(res => {
                    setWait(false);
                    for (let i = 0; i < res.length; i++) {
                        if (array.findIndex(item => item?.ID === res[i].ID) === -1) {
                            array.push(res[i]);
                        }
                    }
                    setArray([...array]);
                })
        } else {
            setWait(false);
        }
    }, []);

    return <UsersList
        page={PageNames.subscribers}
        getUsersFunc={getSubs}
        array={array}
        wait={wait}
        setArray={SetArray}
        setWait={SetWait}
        index={index}
        setIndex={SetIndex}
    />
});