import React, {FC, useEffect, useMemo, useRef} from 'react';
import {UserLine} from "./UI/UserLine";
import classes from "../../styles/MainPart.module.scss"
import {TextToEmpty} from "./UI/TextToEmpty";
import {SetLoading} from "../../helper/SetLoading";
import {User} from "../../types/FriendsLists";
import {CoolUser, UsersStore} from "../../store/UsersStore";
import {USERID} from "../../index";
import {PageId} from "../../helper/GetPage";
import {observer} from "mobx-react-lite";
import {PageNames} from "../MainPart";

interface Props {
	getUsersFunc: () => void;
	array: User[];
	setArray: (array: User[]) => void;
	wait: boolean;
	setWait: (wait: boolean) => void;
	index: number;
	setIndex: (index: number) => void;
	page: PageNames;
}

export const UsersList: FC<Props> = observer(({getUsersFunc, setWait, wait, setArray, array, index, setIndex, page}) => {

	const observer = useRef<IntersectionObserver>();
	const LastUserLineInList = useRef() as React.MutableRefObject<HTMLInputElement>;
	const callback = function (entries:any) {
		if (entries[0].isIntersecting) {
			setIndex(index + 1);
		}
	};
	const emptyListMessage = useMemo(() => {
		const ownerCondition = USERID && PageId === +USERID;
		switch (page) {
			case PageNames.friends: {
				if (ownerCondition) {
					return 'У вас нет друзей';
				} else {
					return 'У этого пользователя нет друзей';
				}
			}
			case PageNames.subscribers: {
				if (ownerCondition) {
					return 'У вас нет подписчиков';
				} else {
					return 'У этого пользователя нет подписчиков';
				}
			}
			case PageNames.mutualFriends: {
				return 'У вас нет общих друзей с этим пользователем';
			}
			case PageNames.onlineFriends: {
				if (ownerCondition) {
					return 'У вас нет друзей онлайн';
				} else {
					return 'У этого пользователя нет друзей онлайн';
				}
			}
		}
	}, [page])

	useEffect( () => {
		if (observer.current) {
			observer.current.disconnect();
		}
		if (LastUserLineInList.current) {
			observer.current = new IntersectionObserver(callback);
			observer.current.observe(LastUserLineInList.current);
		}
	}, [index]);

	useEffect(() => {
		getUsersFunc();
	}, [index])

	if (wait) {
		return (
			<div id={"AllFriendsBlock"} style={{height: "100%"}}>
				<SetLoading/>
			</div>
		)
	} else {
		return (
			<div>
				<div className={classes.UserList}>
					{
						array.length > 0
							?
							<>
								{
									array.map((value) => {
										return <UserLine key={value.ID} User={value}/>
									})
								}
								<div style={{position: "relative", top: "-30px", height: "5px"}}
									 ref={LastUserLineInList}/>
							</>
							: <TextToEmpty text={emptyListMessage}/>
					}
				</div>
			</div>
		);
	}
});