import React, {memo, FC, useEffect} from 'react';
import classes from '../styles/MainPart.module.scss';
import {VG_SendInvitationToFriend} from "./FC/UI/VG_SendInvitationToFriend";
import {PageNames} from "./MainPart";
import {UsersStore} from "../store/UsersStore";
import {PageId} from "../helper/GetPage";
import {USERID} from "../index";
import {observer} from "mobx-react-lite";

export interface ProbsTitleInMainPart {
    page: PageNames;
    SetPage: (page: PageNames) => void;
}

const TitleInMainPart: FC<ProbsTitleInMainPart> = observer(({page, SetPage}) => {

    const ShowAllFriend = () => {
        SetPage(PageNames.friends);
    },

    ShowOnlineFriend = () => {
        SetPage(PageNames.onlineFriends);
    },

    ShowMutualFriend = () => {
        SetPage(PageNames.mutualFriends);
    },

    ShowOther = () => {
        SetPage(PageNames.subscribers);
    };

    useEffect( () => {
        ShowAllFriend();
    }, [])

    return (
        <div id="TitleInMainPart" className={classes.TitleInMainPart}>
            <div style={{display: 'flex', alignItems: 'center'}}>
                <div className={classes.TitleInMainPart__BlockForBtn + (page === PageNames.onlineFriends ? " " + classes.active : "")} onClick={ShowOnlineFriend} style={{left: "7px"}}>
                    <div className={classes.TitleInMainPart__Btn}>Друзья онлайн</div>
                </div>
                <div className={classes.TitleInMainPart__BlockForBtn + (page === PageNames.friends ? " " + classes.active : "")} style={{width:"96px"}} onClick={ShowAllFriend}>
                    <div className={classes.TitleInMainPart__Btn}>Все друзья</div>
                </div>
                {USERID && PageId !== +USERID
                    &&
                        <div className={classes.TitleInMainPart__BlockForBtn + (page === PageNames.mutualFriends ? " " + classes.active : "")} style={{left: "19.5px"}} onClick={ShowMutualFriend}>
                            <div className={classes.TitleInMainPart__Btn}>
                                {UsersStore.mutualFriends.length > 0
                                    ? UsersStore.mutualFriends.length > 99
                                    ? `Общие друзья (99+)`
                                    : `Общие друзья (${UsersStore.mutualFriends.length})`
                                : `Общие друзья`}
                            </div>
                        </div>
                }
            </div>
            <div className={classes.TitleInMainPart__Btn_Other + (page === PageNames.subscribers ? " " + classes.active : "")} onClick={ShowOther}>
                <VG_SendInvitationToFriend backgroundColor={"#e3f3ff"}/>
            </div>
        </div>
    );
});

export default TitleInMainPart;