import React, {memo, FC} from 'react';

interface Probs {
    text:string;
}

export const TextToEmpty: FC<Probs> = memo(( { text } ) => {

    const styles = {
        TextToEmpty_Block: {
            position: "relative",
            display: "block",

        },
        TextToEmpty: {

        },
        TextToEmpty_Img: {

        }
    };

    return (
        <div style={styles.TextToEmpty_Block as React.CSSProperties}>
            <div style={styles.TextToEmpty as React.CSSProperties}>
                {text}
            </div>
            <div style={styles.TextToEmpty_Img as React.CSSProperties}>

            </div>
        </div>
    );

});