import React from 'react';
import ReactDOM from 'react-dom/client';
import {MainPart} from "./Components/MainPart";
import {Connection} from "./Connection";
import {initFastArrays} from "./libs/fastjs";
import {init as initInfoJS} from "./helper/infojs";
import {init as initHand} from "./store/config";
import {UsersStore} from "./store/UsersStore";
import {MainLayOut} from "./LayOut/MainLayOut";
import {PageId} from "./helper/GetPage";

initInfoJS(true);
initHand()
initFastArrays(false);
export const USERID = localStorage.getItem('userId');

const start = async () => {
    await UsersStore.start();

    (document.getElementById('PrevLoad') as HTMLElement).remove();
    ReactDOM.createRoot( (document.getElementById ("root") as HTMLElement) ).render (
        <MainLayOut>
            <MainPart/>
        </MainLayOut>
    );
}

if (Connection && PageId > 0) {
    start();
} else {
    const cb = () => {
        if (Connection != undefined && PageId > 0) start();
        else interval = setTimeout(cb, 500)
    }
    let interval: any;
    setTimeout(cb, 500)
}