import React, {memo, FC, MouseEvent} from 'react';

interface Probs {
    Text:string;
    Action: Function;
    BackgroundColor?: string;
    color?: string;
    top?: string;
}

export const ButtonInLine: FC<Probs> = memo(( { Text, Action, BackgroundColor = "lightgray", color = "cornflowerblue", top="-6px"} )  => {

    const styles = {
        LineButton: {
            position: "relative",
            display: "inline-flex",
            padding: "2px 4px",
            textAlign: "center",
            backgroundColor: BackgroundColor,
            borderRadius: "4px",
            width: 'auto',
            color: color,
            margin: "0 5px 0 0",
            fontSize: "18px",
            top: top,
            height: '22px',
        },
    };

    const Function = (e: MouseEvent<HTMLDivElement>) => {
        e.stopPropagation();
        Action();
    }

    return (
        <div style={styles.LineButton as React.CSSProperties} className={"noselect"} onClick={Function}>
            {Text}
        </div>
    );

});