import React, {memo, FC, useState, useCallback} from 'react';
import classes from "../styles/MainPart.module.scss"
import TitleInMainPart from "./TitleInMainPart";
import {AllFriends} from "./FC/AllFriends";
import {MutualFriends} from "./FC/MutualFriends";
import {OtherList} from "./FC/OtherList";
import {SetLoading} from "../helper/SetLoading";
import {UsersStore} from "../store/UsersStore";
import {OnlineFriends} from "./FC/OnlineFriends";

export enum PageNames {
    friends,
    subscribers,
    mutualFriends,
    onlineFriends
}

export const MainPart: FC = memo( () => {

    const [page, setPage] = useState<PageNames>(PageNames.friends);
    const SetPage = useCallback((pageName: PageNames) => {
        setPage(pageName);
    }, []);

    if (!UsersStore.mutualFriends) {
        return (
            <div id="MainPart" className={classes.MainPart}>
                <SetLoading/>
            </div>
        )
    }
    else {
        return (
            <div id="MainPart" className={classes.MainPart} style={{overflow: 'hidden'}}>
                <TitleInMainPart SetPage={SetPage} page={page}/>
                <div style={{display: 'flex', flexFlow: 'column', height: '100%', overflow: 'hidden', overflowY: 'scroll'}}>
                    {page === PageNames.onlineFriends && <OnlineFriends/>}
                    {page === PageNames.friends && <AllFriends/>}
                    {page === PageNames.subscribers && <OtherList/>}
                    {page === PageNames.mutualFriends && <MutualFriends/>}
                </div>
            </div>
        );
    }

});