import React, {memo, FC} from 'react';

interface Probs {
    backgroundColor: string;
}

export const VG_SendInvitationToFriend: FC<Probs> = memo(({backgroundColor}) => {

    const styles = {
        VG_SendInvitationToFriend: {
            cursor: "pointer",
            height: "38px",
            display: "inline-block",
            position:"relative",
            marginBottom: "4px",
        },
        Head: {
            position:"relative",
            width: "14px",
            left: "12px",
            top: "3px",
            zIndex: "2",
            backgroundColor: backgroundColor,
            borderRadius: "50%",
            border: "2px solid black",
            height: "14px",
        },
        Body: {
            position:"relative",
            width: "27px",
            left: "5px",
            backgroundColor: backgroundColor,
            borderRadius: "20px 20px 0 0",
            border: "2px solid black",
            height: "12px",
        },
        MailBlock: {
            backgroundColor: backgroundColor,
            position:"relative",
            top: "-8px",
            width: "15px",
            height: "9px",
            zIndex: "2",
            left: "2px",
            border: "solid black 1px",
        },
        MailBlock_Polygon: {
            border: "9px solid transparent",
            borderTop: "7px solid black",
            position: "relative",
            left: "-1px",
        }
    };

    return (
        <div style={styles.VG_SendInvitationToFriend as React.CSSProperties}>
            <div style={styles.Head as React.CSSProperties}/>
            <div style={styles.Body as React.CSSProperties}/>
            <div style={styles.MailBlock as React.CSSProperties}>
                <div style={styles.MailBlock_Polygon as React.CSSProperties}/>
            </div>
        </div>
    );

});