import React, {memo, FC, useRef, useState, useCallback} from 'react';
import classes from '../../../styles/MainPart.module.scss';
import {ButtonInLine} from "./LineButton";
import {LazyImg} from "../../../helper/LazyImg";
import {User} from "../../../types/FriendsLists";
import {USERID} from "../../../index";
import {SetNotification} from "../../../helper/SetNotification";
import {UsersStore} from "../../../store/UsersStore";
import {Loading} from "../../../helper/Loading";
import {DOMAIN} from "../../../helper/api";
import {observer} from "mobx-react-lite";

interface Props {
    User: User,
}

export const UserLine: FC<Props> = observer(({User: User}) => {//TODO Сейчас при удалении из друзей, если это не твои друзья сейчас отображены, появляется кнопка отправить запрос дружбы, а должна появится кнопка принять запрос

    console.log(UsersStore.yourselves)
    let avatar: string;
    const Myself = useRef<HTMLDivElement>(null);
    const [IsFriend, SetIsFriend] = useState(UsersStore.yourselves != null && UsersStore.yourselves.friends.indexOf(User.ID) != -1);
    const [IsYourSubscriber, SetIsYourSubscriber] = useState(UsersStore.yourselves != null && UsersStore.yourselves.subscribers.indexOf(User.ID) != -1);
    const [AreYouSubscriber, SetAreYouSubscriber] = useState<boolean>((USERID && User.ID != +USERID && User.Subscribers.indexOf(+USERID) != -1) as boolean);

    if ( User.Avatar != '' ) {
        avatar = DOMAIN + '/UserFiles/' + User.ID + '/Image/' + User.Avatar
    } else {
        avatar = DOMAIN + '/UserFiles/NULL.png'
    }

    function Href(): void {
        window.location.href = DOMAIN + `/pages/profile/?userId=${User.ID}`;
    }

    const OpenTheChat = useCallback (() => { //todo
        SetNotification("Вы открыли переписку");
    }, []);
    const AddFriend = useCallback( () => {
            SetIsFriend(true);
            SetIsYourSubscriber(false);
            UsersStore.addFriend(User.ID)
        }, [])
    const RemoveFromFriendList = useCallback (() => {
        SetIsFriend(false);
        SetIsYourSubscriber(true);
        UsersStore.deleteFriend(User.ID)
    }, [])
    const SendAFriendshipRequest = useCallback (() => {
        SetAreYouSubscriber(true);
        UsersStore.subscribe(User.ID)
    }, []);
    const TakeBackFriendShipRequest = useCallback( () => {
        SetAreYouSubscriber(false);
        UsersStore.unsubscribe(User.ID)
    }, []);

    return (
        <div ref={Myself} className={classes.UserLine}>
            <LazyImg element={<Loading/>} ImageClassName={classes.UserLine__Block_Img} DivToImageClassName={classes.UserLine__Block_Avatar} Onclick={Href} src={avatar}/>
            <div className={classes.UserLine__Block_Main} onClick={Href}>
                <div>
                    {User.Name + ' ' + User.Surname}
                </div>
                <ButtonInLine Text={"Открыть переписку"} Action={OpenTheChat} top={"0"}/>
                {IsYourSubscriber != undefined
                    ?
                        IsFriend
                            ? <ButtonInLine Text={"Удалить из друзей"} Action={RemoveFromFriendList} BackgroundColor={"red"} color={"snow"} top={"0"}/>
                            :
                                IsYourSubscriber
                                    ? <ButtonInLine Text={"Добавить в друзья"} Action={AddFriend} top={"0"}/>
                                    : AreYouSubscriber
                                        ? <ButtonInLine Text={"Отменить запрос дружбы"} Action={TakeBackFriendShipRequest} top={"0"} color={"red"}/>
                                        : <ButtonInLine Text={"Отправить запрос дружбы"} Action={SendAFriendshipRequest} top={"0"}/>
                    :
                        ''
                }
            </div>
        </div>
    );
});